import { Outlet } from "react-router-dom";
import { Container } from "reactstrap";

const SimpleLayout = () => {
  return (
    <main>
      <div className="pageWrapper d-lg-flex" style={{ ...styles.pannelStyle }}>
        {/********Content Area**********/}

        <div className="contentArea">
          {/********Middle Content**********/}
          <Container className="p-4 wrapper" fluid>
            <Outlet />
          </Container>
        </div>
      </div>
    </main>
  );
};

const styles = {
  pannelStyle: {
    border: innerHeight * 0.05,
    borderColor: "white",
    borderStyle: "solid",
    borderRadius: 20,
    margin: 0,
    padding: 0,
  },
};

export default SimpleLayout;
